import React, { useState, useContext } from "react";
import { makeStyles, useTheme, Menu, MenuItem, Button } from "@material-ui/core";
import ThemeContext from "../../contexts/themeContext";
import { KeyboardArrowDownSharp } from "@material-ui/icons";
import i18n from "i18next";
import { useTranslation } from "react-i18next";
import clsx from "clsx";
import "flag-icon-css/css/flag-icon.min.css";

const languages = [
    {
        code: "pt",
        name: "PT",
        country_code: "pt",
    },
    {
        code: "en",
        name: "EN",
        country_code: "gb",
    },
];

const LangSelector = ({mobile, ...props}) => {
    const theme = useTheme();
    const { isDarkMode } = useContext(ThemeContext);
    const classes = useStyles(theme);

    const { t } = useTranslation();
    const [anchorEl, setAnchorEl] = useState(null);

    const handleClose = (code) => {
        i18n.changeLanguage(code);
        setAnchorEl(null);
        if (props.onClose) {
            props.onClose();
        }
    };

    const currentLanguage = languages.find((elem) => elem.code === t("language"));

    return (
        <div {...props} className={clsx({ [classes.divWithMaxWidth]: mobile })}> 
            <Button variant="outlined" color="primary" className={classes.langSelectBtn} onClick={(e) => setAnchorEl(e.currentTarget)}>
                {currentLanguage && (
                    <>
                        <span className={`flag-icon flag-icon-${currentLanguage.country_code} ${classes.flagIcon}`} />
                        {currentLanguage.name}
                    </>
                )}
                <KeyboardArrowDownSharp className={classes.arrow} />
            </Button>
            <Menu
                id="profile-menu"
                anchorEl={anchorEl}
                open={Boolean(anchorEl)}
                onClose={() => setAnchorEl(null)}
                keepMounted
                elevation={0}
                getContentAnchorEl={null}
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "center",
                }}
                transformOrigin={{
                    vertical: "top",
                    horizontal: "center",
                }}
                className={`${clsx(classes.profileMenu, { [classes.profileMenuDark]: isDarkMode })} ${classes.langSelectItem}`}
                disableScrollLock
            >
                <MenuItem className={clsx(classes.menuItem, { [classes.menuItemDark]: isDarkMode })} onClick={() => handleClose("en")}>
                    <span className={`flag-icon flag-icon-gb ${classes.flagIcon}`} />
                    EN
                </MenuItem>
                <MenuItem className={clsx(classes.menuItem, { [classes.menuItemDark]: isDarkMode })} onClick={() => handleClose("pt")}>
                    <span className={`flag-icon flag-icon-pt ${classes.flagIcon}`} />
                    PT
                </MenuItem>
            </Menu>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    divWithMaxWidth: {
        maxWidth: "100%",
    },
    langSelectBtn: {
        width: "110px",
    },
    langSelectItem: {
        width: "110px",
        "& .MuiPaper-root": {
            width: "110px",
            minWidth: "110px",
        }
    },
    arrow: {
        color: theme.palette.primary.main,
    }, 
    profileMenu: {
        "& .MuiPaper-root": {
            backgroundColor: theme.palette.background.default,
            borderStyle: "solid",
            borderWidth: "1px",
            borderColor: theme.palette.primary.main,
        },
        "& ul": {
            padding: "0px",
        },
    },
    profileMenuDark: {
        "& .MuiPaper-root": {
            backgroundColor: theme.palette.background.default,
            borderStyle: "solid",
            borderWidth: "1px",
            borderColor: theme.palette.action.disabledBackground,
        },
        "& ul": {
            padding: "0px",
        },
    },
    menuItem: {
        fontFamily: theme.typography.fontFamily,
        fontSize: "16px",
        color: theme.logoColor.primary,
        "&:hover": {
            backgroundColor: theme.palette.secondary.main, // theme.palette.action.disabledBackground : ,
            color: theme.logoColor.primary, // theme.palette.background.default : ,
        },
    },
    menuItemDark: {
        fontFamily: theme.typography.fontFamily,
        fontSize: "16px",
        color: theme.logoColor.primary,
        "&:hover": {
            backgroundColor: theme.palette.action.disabledBackground, //  : theme.palette.secondary.main,
            color: theme.palette.background.default, //  : theme.logoColor.primary,
        },
    },
    flagIcon: {
        marginRight: theme.spacing(1),
    },
}));

export default LangSelector;
