import React from "react";
import { useLocation, useHistory } from "react-router-dom";
import { Link } from "react-scroll";
import { makeStyles } from "@material-ui/core";
import FilipeLopesPires from "../../assets/images/FilipeLopesPires";

const Logo = ({ setHomeIsActive, ...rest }) => {
    const classes = useStyles();
    const location = useLocation();
    const history = useHistory();
    
    return (
        <Link
            spy
            smooth
            duration={500}
            offset={-70}
            to="home"
            onSetActive={() => setHomeIsActive(true)}
            onSetInactive={() => setHomeIsActive(false)}
            onClick={() => checkHomePage(location, history)}
            className={classes.root}
        >
            <FilipeLopesPires {...rest} />
            {/* Filipe Lopes Pires */}
        </Link>
    );
};

const useStyles = makeStyles((theme) => ({
    root: {
        cursor: "pointer",
    },
}));

function checkHomePage(location, history) {
    if(location.pathname === '/') {
        return;
    } else {
        history.push('/');
    }
}

export default Logo;
