import React, { useContext } from "react";
import ThemeContext from "../../contexts/themeContext";
import { makeStyles, Drawer, List, Button, Divider, ListItem, Link as MuiLink } from "@material-ui/core";
import { Link } from "react-scroll";
import DarkModeSwitcher from "../DarkModeSwitcher";
import LangSelector from "./LangSelector";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";

const MobileMenu = ({ open, onClose, onOpen }) => {
    const classes = useStyles();
    const { t } = useTranslation();
    const location = useLocation();
    const { isDarkMode } = useContext(ThemeContext);

    const listItemProps = {
        button: true,
        component: Link,
        onClick: onClose,
        onKeyDown: onClose,
        spy: true,
        smooth: true,
        offset: 0,
        duration: 500,
        className: classes.listItem,
        activeClass: isDarkMode ? classes.activeDark : classes.active,
    };

    return (
        <Drawer anchor="left" open={open} onClose={onClose} classes={{ paper: classes.drawer }}>
            <div className={classes.list} role="presentation">
                {location.pathname === '/' && (
                    <List className={classes.fullList}>
                        <ListItem {...listItemProps} to="immersive_section">
                            {t('menu_immersive_section')}
                        </ListItem>
                        <ListItem {...listItemProps} to="film_section">
                            {t('menu_film_section')}
                        </ListItem>
                        <ListItem {...listItemProps} to="design_section">
                            {t('menu_design_section')}
                        </ListItem>
                        <ListItem {...listItemProps} to="about">
                            {t('menu_about')}
                        </ListItem>
                    </List>
                )}
                <List className={classes.fullList}>
                    <ListItem className={classes.btnContainer}>
                        <Button
                            component={MuiLink}
                            href="/FilipePires_CV.pdf"
                            variant="outlined"
                            color="primary"
                            underline="none"
                            className={classes.resumeBtn}
                            >
                            {t('menu_resume')}
                        </Button>
                    </ListItem>
                    <ListItem className={classes.btnContainer}>
                        <LangSelector mobile={true} />
                    </ListItem>
                    <ListItem className={classes.btnContainer}>
                        <DarkModeSwitcher />
                    </ListItem>
                    <Divider />
                </List>
            </div>
        </Drawer>
    );
};

const useStyles = makeStyles((theme) => ({
    drawer: {
        backgroundColor: theme.palette.background.default,
    },
    list: {
        width: 250,
    },
    fullList: {
        width: "auto",
        marginTop: theme.spacing(4),
    },
    listItem: {
        display: "flex",
        justifyContent: "center",
        padding: theme.spacing(2, 0),
        color: theme.palette.action.disabled,
        "&:hover": {
            backgroundColor: theme.palette.primary.main,
            color: theme.palette.background.default,
        },
    },
    btnContainer: {
        display: "flex",
        justifyContent: "center",
        marginTop: theme.spacing(1),
    },
    active: {
        backgroundColor: theme.palette.primary.main,
    },
    activeDark: {
        backgroundColor: theme.palette.action.disabledBackground,
    },
    resumeBtn: {
        width: "110px",
        minWidth: "110px",
    },
}));

export default MobileMenu;
