import React, { useState, useEffect, useContext } from "react";
import { Tab, Tabs, Button, makeStyles, withStyles, Link as MuiLink } from "@material-ui/core";
import { motion, useAnimation } from "framer-motion";
import { Link, Events } from "react-scroll";
import LangSelector from "./LangSelector";
import LoaderContext from "../../contexts/loaderContext";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";

const smoothScrollProps = {
    spy: true,
    smooth: true,
    offset: -70,
    duration: 500,
};

const AnimatedLink = React.forwardRef((props, ref) => (
    <motion.div ref={ref} custom={props.custom} animate={props.animate}>
        <Link {...smoothScrollProps} {...props} />
    </motion.div>
));

const Menu = ({homeIsActive}) => {
    const classes = useStyles();
    const [value, setValue] = useState(false);
    const [isScrolling, setIsScrolling] = useState(false);
    const { isLoading } = useContext(LoaderContext);
    const controls = useAnimation();
    const { t } = useTranslation();
    const location = useLocation();

    useEffect(() => {
        Events.scrollEvent.register("begin", (to, element) => {
            setIsScrolling(true);
        });

        Events.scrollEvent.register("end", (to, element) => {
            setIsScrolling(false);
        });
    });

    useEffect(() => {
        if (!isLoading) {
            controls.start((i) => ({
                y: 0,
                opacity: 1,
                transition: { delay: i * 0.1 + 0.3 },
            }));
        } else {
            controls.start({ opacity: 0, y: -5 });
        }
    }, [isLoading, controls]);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const spyHandleChange = (index) => {
        if (!isScrolling) {
            setValue(index);
        }
    };

    useEffect(() => {
        if (homeIsActive){
            setValue(false)
        }
    }, [homeIsActive])

    return (
        <div className={classes.wrapper}>
            {location.pathname === '/' && (
                <StyledTabs
                    className={classes.tabs}
                    value={value}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleChange}
                    aria-label="disabled tabs example"
                >
                    <StyledTab
                        component={AnimatedLink}
                        animate={controls}
                        custom={0}
                        to="immersive_section"
                        label={t('menu_immersive_section')}
                        onSetActive={() => spyHandleChange(0)}
                    />
                    <StyledTab
                        component={AnimatedLink}
                        animate={controls}
                        custom={1}
                        to="film_section"
                        label={t('menu_film_section')}
                        onSetActive={() => spyHandleChange(1)}
                    />
                    <StyledTab
                        component={AnimatedLink}
                        animate={controls}
                        custom={2}
                        to="design_section"
                        label={t('menu_design_section')}
                        onSetActive={() => spyHandleChange(2)}
                    />
                    <StyledTab
                        component={AnimatedLink}
                        custom={3}
                        animate={controls}
                        to="about"
                        label={t('menu_about')}
                        onSetActive={() => spyHandleChange(3)}
                        onSetInactive={()=>spyHandleChange(false)}
                    />
                </StyledTabs>
            )}
            <motion.div custom={2} animate={controls}>
                <Button
                    component={MuiLink}
                    href="/FilipePires_CV.pdf"
                    variant="outlined"
                    color="primary"
                    underline="none"
                    className={classes.resumeBtn}
                >
                    {t('menu_resume')}
                </Button>
            </motion.div>
            <motion.div custom={3} animate={controls}>
                <LangSelector mobile={false} style={{ marginLeft: "32px" }} />
            </motion.div>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    wrapper: {
        display: "flex",
        alignItems: "center",
    },
    tabs: {
        marginRight: theme.spacing(4),
    },
    navMenuItem: {
        marginRight: theme.spacing(1),
    },
    resumeBtn: {
        width: "5vw",
        minWidth: "110px",
    },
}));

const StyledTab = withStyles((theme) => ({
    root: {
        transition: ".2s",
        minWidth: 120,
        color: theme.palette.action.disabled,
        "&:hover": {
            color: theme.palette.primary.main,
        },
    },
}))((props) => <Tab disableRipple {...props} />);

const StyledTabs = withStyles({
    indicator: {
        "& > span": {
            maxWidth: 20,
        },
    },
})((props) => <Tabs {...props} variant="fullWidth" TabIndicatorProps={{ children: <span /> }} />);

export default Menu;
